import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCardContent, IonItem, IonIcon, IonLabel, IonButton, IonGrid, IonRow, IonCol } from '@ionic/react';
import { pin, wifi, wine, warning, walk } from 'ionicons/icons';

import React,{useEffect,useState} from 'react';

const Work: React.FC = () => {

  const [websiteDetails,setWebsiteDetails] = useState();

  useEffect(async ()=>{
    await fetch("./project.json")
      .then(res => res.json())
      .then(
        (result) => {
          setWebsiteDetails(result);
        },
        (error) => {
          console.log(error)
        }
      )
  },[])

  return (
      <>

      <IonContent className="my_work" fullscreen>
        <IonGrid>
          <IonRow>
            {websiteDetails != undefined && websiteDetails.map((websiteDetailsArray, index) => (
            <IonCol size-lg="4" size-md="12" size-sm="12" size="12" key={index} >
              <IonCard>
                <img src={process.env.PUBLIC_URL+`/assets/${websiteDetailsArray.img}`} />
                <IonCardHeader>
                  <IonCardSubtitle></IonCardSubtitle>
                  <IonCardTitle>{websiteDetailsArray.title}</IonCardTitle>
                </IonCardHeader>
                <IonCardContent>
                  <IonButton href={websiteDetailsArray.link} target="_blank">Website Link</IonButton>
                </IonCardContent>
              </IonCard>
            </IonCol>
            ))}
            
          </IonRow>
        </IonGrid>
      </IonContent>

      </>
  );
};

export default Work;
