import React,{useEffect,useState} from 'react';
import { getPlatforms } from '@ionic/react';
import {Web,Mobile} from './screen/'

export const App = () => {

  const [mScreen, setMScreen] = React.useState<any>({
    matches: getPlatforms()[0] === 'desktop' ? true : false,
  });


  useEffect(() => {
    let mediaQuery = window.matchMedia("(min-width: 1024px)");
    mediaQuery.addListener(setMScreen);
    return () => mediaQuery.removeListener(setMScreen);
  },[]);

  return(
    <>
      {mScreen && mScreen.matches === true ? <Web /> : <Mobile />}
    </>
  );
};

