import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { IonApp, IonRouterOutlet, IonTabs, IonTabBar, IonTabButton, IonIcon, IonLabel, IonBadge, getPlatforms, IonPage } from '@ionic/react';
import { calendar, personCircle, map, informationCircle,homeOutline, laptopOutline, bookOutline } from 'ionicons/icons';

import { IonReactRouter } from '@ionic/react-router';
import Home from '../pages/Home';

import '@ionic/react/css/core.css';

import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

import '../theme/variables.css';
import Work from '../pages/Work';
import { MobileNav } from '../components/MobileNav';
import Resume from '../pages/Resume';

import {WrapperRoute} from '../routes/routes'

export const Web: React.FC = () => (
  <IonApp>
    <IonReactRouter>
      <MobileNav />
      <IonRouterOutlet  id="main">
        <WrapperRoute path="/home" component={Home} exact={true} />
        <WrapperRoute path="/works" component={Work} exact={true} />
        <WrapperRoute path="/resume" component={Resume} exact={true} />
        <Redirect to = "/home" />
      </IonRouterOutlet>
    </IonReactRouter>
  </IonApp>
);

export const Mobile: React.FC = () => (
  <IonApp>
    <IonReactRouter>
      
        <IonTabs>
          <IonRouterOutlet>
            <Route path="/:tab(home)" component={Home} exact={true} />
            <Route path="/:tab(works)" component={Work} exact={true} />
            <Route path="/:tab(resume)" component={Resume} />
            <Route exact path="/" render={() => <Redirect to="/home" />} />
          </IonRouterOutlet>
          <IonTabBar slot="bottom">
            <IonTabButton tab="home" href="/home">
              <IonIcon icon={homeOutline} />
              <IonLabel>Home</IonLabel>
            </IonTabButton>
            <IonTabButton tab="works" href="/works">
              <IonIcon icon={laptopOutline} />
              <IonLabel>My Work</IonLabel>
            </IonTabButton>
            <IonTabButton tab="resume" href="/resume">
              <IonIcon icon={bookOutline} />
              <IonLabel>Resume</IonLabel>
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
     
    </IonReactRouter>
  </IonApp>
);


