import { IonSlides, IonSlide, IonImg } from '@ionic/react';
import React,{useEffect,useState} from 'react';

export const Slider: React.FC = () => {
  
  const [websiteDetails,setWebsiteDetails] = useState();

  useEffect(async ()=>{
    await fetch("./project.json")
      .then(res => res.json())
      .then(
        (result) => {
          setWebsiteDetails(result);
        },
        (error) => {
          console.log(error)
        }
      )

      const slides = document.getElementById('slides')
      slides.options = {
        spaceBetween: 40,
        autoplay: {
          delay: 2000,
        },
      }
  },[])

  return (
    <IonSlides pager="true" id="slides">
        <IonSlide>
            <IonImg src={process.env.PUBLIC_URL+`/assets/banner.png`} className="slider"></IonImg>
        </IonSlide>
        {websiteDetails != undefined && websiteDetails.map((websiteDetailsArray, index) => (
            <IonSlide key={index}>
                <IonImg src={process.env.PUBLIC_URL+`/assets/${websiteDetailsArray.img}`} className="slider"></IonImg>
            </IonSlide>
        ))}
    </IonSlides>
  );
};

